import React from "react";
import StartPage from "./assets/components/StartPage";
import QuizPage from "./assets/components/QuizPage";
import ScoreBoard from "./assets/components/ScoreBoard";

function App() {
  const [quizzical , setQuizzical] = React.useState(false)
  const [quizQues, setQuizQues] = React.useState([])
  
  React.useEffect(() => {
    async function fetchData(){
      const res = await fetch("https://opentdb.com/api.php?amount=10&type=multiple")
      const data = await res.json()
      console.log('something')
      // console.log(score)
      setQuizQues(data.results)
    }
    fetchData()
  },[0])
  

  console.log(`%c${JSON.stringify(quizQues)}`, "color:green")
  const [score, setScore] = React.useState(0)
  const [questNum, setQuestNum] = React.useState(0)
  
  

  return (
    <div className="app">
      {!quizzical ?
      <StartPage someFunc={() => {
        setQuizzical(true)
        console.log("Start Quiz btn clicked")
        }}/>
         : questNum < 10 ?
         <QuizPage 
         data={quizQues} 
          score={score}
          questNum={questNum} 
          setScore={(value) => setScore(value)}
          setQuestNum={(value) => setQuestNum(value)}
           />
         : <ScoreBoard 
          score={score}
         /> 
       }
    </div>
  );
}

export default App;
