import {nanoid} from "nanoid"
import React from "react"

function QuizPage(props){

    const [optsArr,setOptsArr] = React.useState([])
    
    
    /* Used for getting an option array */
    React.useEffect(() =>{
        console.log("First call on mount..");
        let newOptsArr = [...props.data[props.questNum].incorrect_answers , props.data[props.questNum].correct_answer]
        newOptsArr = newOptsArr.map(opt => ({key:nanoid(),id:nanoid(),'option':opt}))
        
        setOptsArr(newOptsArr.sort(() => Math.random() - 0.5))  
        return () => console.log("Cleanup..");
    },[props.questNum])
    /* ---------------------------------- */

    
    /* function to change Question */
    const changeQues = () => {
        if(props.questNum +1 <= props.data.length){
            props.setQuestNum(prevNum => prevNum+1)
        }
        setDisabled(prevState => !prevState)

    }
    
    const submit = () =>{
        props.setQuestNum(prevNum => prevNum+1)
    }

    /* ---------------------------- */
    const handleChange = (event) => {
        let { value } = event.target
        if(value === props.data[props.questNum].correct_answer){
            props.setScore(prevScore => prevScore+1)
        }
        setDisabled(prevState => !prevState)

    }
    const [disabled,setDisabled] = React.useState(false)
    // const stylesIfWrong = {backgroundColor:"#e30000"}

    // const [styleButton, setStyleButton] = React.useState(false)

    /*Generating radio buttons with label for options*/
    const optElems = optsArr.map(item => {
        return (
            <>
                <input 
                    type="radio" 
                    key={item.key}
                    id={item.id} 
                    name={props.data[props.questNum].question}
                    value={item.option}
                    className="radio"
                    onChange={(event) =>handleChange(event)}
                    disabled={disabled}
                    />
                <label
                    htmlFor={item.id}
                    dangerouslySetInnerHTML={{__html: item.option}}
                    // style={styleButton ? styles : {background:"#fff"}}
                ></label>
            </>
        )
    })

    /* ---------------------------- */

    return (
        <main className="quizPage flex--col">
            <span className="score">SCORE : {props.score}</span>
            <h1>Question {props.questNum+1}/10</h1>
            <form className="flex--col" onSubmit={(e)=> e.preventDefault()}>
                <p dangerouslySetInnerHTML={{__html: props.data[props.questNum].question}}></p>
                <fieldset>
                    {optElems}
                </fieldset>
            <button 
                className="start-quiz-btn" 
                onClick={() => props.questNum<9? changeQues() : submit()}>
                    <span>{props.questNum < 9? "Next Question":"Submit"}</span>
            </button>
            </form>  
        </main>
    )
}
export default QuizPage;